<template>
  <p>Main View, nigga!</p>
  <div id="map"></div>
</template>

<script>
import axios from 'axios'
export default {

  data () {
    return {
      login: '',
      password: '',
      error: false,
      responseData: []
    }
  },
  mounted() {
    let firstScript = document.createElement('script')
    firstScript.setAttribute('src', 'https://api-maps.yandex.ru/2.1/?lang=ru_RU&amp;apikey=5e899c8c-6332-4541-89fe-3a98ecd486d6')
    document.head.appendChild(firstScript)
    //let Script = document.createElement('script')
    firstScript.setAttribute('src', 'mapbasics.js')
    document.head.appendChild(firstScript)
  },
  methods: {
    LoginUser () {
      const user = JSON.stringify({ login: this.login, password: this.password })
      const customConfig = {
        headers: {
          'Content-Type': 'application/json'
        }
      }
      axios.post('api/account/signin', user, customConfig).then(response => {
        const temp = response.data
        VueCookies.set('token' , temp.data.token)
        this.$router.push('/main')
      }).catch((error) => {
        this.error = true
        console.log(error.response.status)
      })
    }
  }
}
</script>
